<template>
  <div>
    <header>
      <navbar>
      </navbar>
    </header>
    <section class="main">
      <p class="main-nav">
        <router-link to="/">
          总览
        </router-link>
        <span>> >{{this.$route.query.repotag}}</span>
      </p>
      <section class="main-wrapper">
        <div class="brief">
          <p>{{this.$route.query.repotag}}</p>
          <span v-if="tagNum===''"><i>标签数：</i>--</span>
          <span v-else><i>标签数：</i>{{tagNum}}个 </span>
          <!--<span><i>SIZE：</i>{{Math.round(net.size / 1024/1024)}}MB</span>-->
          <span><i>网页更新时间：</i>{{this.time}}</span>
          <!--<button class="collection">-->
          <!--<i class="iconfont icon-heart">-->
          <!--&#xe631;</i>-->
          <!--收藏-->
          <!--</button>-->
          <!--<button class="subscribe">-->
          <!--<i class="iconfont icon-subscriber">-->
          <!--&#xe768;</i>-->
          <!--订阅-->
          <!--</button>-->
        </div>
        <div class="sub-nav">
          <el-menu :default-active="activeIndex" mode="horizontal">
            <el-menu-item index="1">
              <router-link :to="{ path: 'index', query: { repotag: this.$route.query.repotag }}">
                概述
              </router-link>
            </el-menu-item>
            <el-menu-item index="2">
              <router-link :to="{ path: 'strategy', query: { repotag: this.$route.query.repotag }}">
                策略
              </router-link>
            </el-menu-item>
            <el-menu-item index="3">
              <router-link :to="{ path: 'content', query: { repotag: this.$route.query.repotag }}">
                内容
              </router-link>
            </el-menu-item>
            <el-menu-item index="4">
              <router-link :to="{ path: 'security', query: { repotag: this.$route.query.repotag }}">
                安全
              </router-link>
            </el-menu-item>
          </el-menu>
        </div>
        <transition name="fade" mode="out-in">
          <keep-alive>
            <router-view v-bind:net="net"></router-view>
          </keep-alive>
        </transition>
      </section>
    </section>
  </div>
</template>
<script type="text/ecmascript-6">
  import Navbar from 'components/navbar'
  import { fetchImage, getTagNum } from '@/api/transaction'

  export default {
    name: 'index',
    components: {
      Navbar
    },
    data() {
      return {
        activeIndex: '1',
        net: {},
        time: '',
        tagNum: ''
      }
    },
    methods: {},
    created() {
      const url = this.$route.query.repotag
      fetchImage(url)
        .then(res => {
          this.net = res.data
        })
      getTagNum(url)
        .then(res => {
          this.tagNum = res.data['Tag Name'].length
        })
      this.time = new Date().toLocaleString()
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .main
    width 88%
    margin: 0 auto
    position: relative
    top: 58px
    .main-nav
      font-size 1.6em
      color: #525252
      padding: 50px 0 25px 15px
      border-bottom: #E5E5E5 solid 1.2px
      a
        color: #4470E9
    .brief
      width: 91%
      margin: 0 auto
      padding: 0 0 25px 0
      p
        font-size 1.6em
        color: #333333
        font-weight: 600
        margin: 45px 0 20px 0
      span
        font-size: 14px
        color: #4D4D4D
        margin: 0 35px 0px 0
        i
          color: #808080
          padding: 0px 2px 5px 0
      button
        line-height: 30px
        padding: 0 28px
        margin: -12px 0 0 10px
        border: #3E7AF6 solid 1px
        float right
        top: 10px
      i
        display: inline-block
        margin: -3px 2px 0 0
      .subscribe
        color: #FFFFFF
        background: #3E7AF6
      .collection
        color: #3E7AF6
        background: #FFFFFF

  .main .brief[data-v-181e0de3]
    margin-left: 0px;

  section
    margin-left 60px
    margin-right 60px

  .icon-heart
    color: #3E7AF6
    font-size: 1.05em
    font-weight: 500

  .icon-subscriber
    color: #FFFFFF
</style>



